import React from 'react';
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';
import './Slideshow.css'
import { images } from '../../portfolio'

const Slidess = () => {
   
    return (
        <div className='slide-container'>
        <Slide>
            
            <div className="each-slide-effect ">
                <div style={{ 'backgroundImage': `url(${images[0]})` }}>
                    <span> Runners Up Prize from Deputy PM of Singapore and Education Minister Of India</span>
                </div>
            </div>
            <div className="each-slide-effect">
                <div style={{ 'backgroundImage': `url(${images[1]})`}}>
                    <span>Winnners Prize from Vice President and Education Minister Of India</span>
                </div>
            </div>
            <div className="each-slide-effect">
                <div style={{ 'backgroundImage': `url(${images[2]})` }}>
                    <span style={{'color':'black'}}> UNESCO-India International Hackathon</span>
                </div>
            </div>
            <div className="each-slide-effect">
                <div style={{ 'backgroundImage': `url(${images[3]})` }}>
                    <span style={{'color':'white'}}>Karnataka Police Hackathon</span>
                </div>
            </div>
            <div className="each-slide-effect">
                <div style={{ 'backgroundImage': `url(${images[4]})` }}>
                    <span style={{'color':'white'}}>Joint Winner Smart India Hackathon</span>
                </div>
            </div>
        </Slide>
        </div>
    );
};

export default Slidess;